import Vue from 'vue'
import Vuex from 'vuex'
import defaultBoard from './default-board'
import { saveStatePlugin, uuid } from './utils'

Vue.use(Vuex)

const board = JSON.parse(localStorage.getItem('board')) || defaultBoard

export default new Vuex.Store({
  plugins: [saveStatePlugin],
  state: {
    board
  },
  getters: {
    getTask (state) {
      return (id) => {
        for (const column of state.board.columns) {
          for (const task of column.tasks) {
            if (task.id === id) {
              return task
            }
          }
        }
      }
    }
  },
  mutations: {
    CREATE_TASK (state, { tasks, name }) {
      tasks.push({
        name,
        id: uuid(),
        description: ''
      })
    },
    CREATE_COLUMN (state, { name }) {
      state.board.columns.push({
        name,
        tasks: []
      })
    },
    UPDATE_TASK (state, { task, key, value }) {
      task[key] = value
    },
    MOVE_TASK (state, { columnIndex, value }) {
      state.board.columns[columnIndex].tasks = value
    },
    UPDATE_BOARD (state, value) {
      state.board.columns = value
    },
    REMOVE_TASK (state, { columnIndex, taskIndex }) {
      const taskList = state.board.columns[columnIndex].tasks
      taskList.splice(taskIndex, 1)
    },
    REMOVE_COLUMN (state, columnIndex) {
      state.board.columns.splice(columnIndex, 1)
    }
  }
})
